html{
  font-family: sans-serif;
}
body {
  font-family: Helvetica,STHeiti STXihei,Microsoft Yahei,Tohoma,Arial;
  color: #3b3b3b;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color: rgba(0,0,0,0);
  -webkit-tap-highlight-color: transparent;
}
*, *::before, *::after {
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
  monospace;
}
.flex-block {      /* 老语法 iOS 6-, Safari 3.1-6 */         /* 老语法 Firefox 19- (buggy but mostly works) */      /* 过渡语法 IE 10 */
    display: -webkit-flex;     /* 新语法 Chrome */
    display: flex;             /* 新语法, Spec - Opera 12.1, Firefox 20+ */
}
.flex_row_between{
  -webkit-flex-direction:row;
          flex-direction:row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.flex-9{
  -webkit-flex:9 1;
          flex:9 1;
}
.flex-1{
  -webkit-flex:1 1;
          flex:1 1;
}
.main{
  max-width: 1130px;
  margin: 0 auto;
}



/*Header-----------------------------------------------------*/
.header {
  padding: 10px 40px 10px 40px;
  height: 80px;
  line-height: 76px;
  text-align: left;
  background-color: #fcfcfc;
  margin-bottom: 24px;
}
.header .logo{
  font-size: 28px;
  font-weight: bold;
  font-family: "Microsoft YaHei UI ";
  color: #000;
}
.header .sign_container{
  width: 200px;
}

/*Nav-----------------------------------------------------*/
.nav{
  height: 50px;
  line-height: 46px;
  background-color: #f8f8f8;
  box-shadow: 0 1px 2px 0 rgba(0,0,0,.1);
  margin-bottom: 34px;
}
/*固定导航栏--------------------------------*/
.nav.fixed{
  width: 100%;
  position: fixed;
  z-index: 12;
  top: 0;
}
.nav_item{
  text-align: center;
  z-index: 20;
  font-size: 14px;
  color: #3b3b3b;
  margin: 14px 1.6% 0;
  text-decoration: none;
  white-space: nowrap;
}
.nav_item:first-child {
  margin-left: 0;
}
.nav_item:hover{
  color:#00bfff;
}
.nav_item_active {
  position: relative;
  color: #00bfff;
  cursor: default;
}
.nav_item_active:after {
  position: absolute;
  top: 24px;
  left: 0;
  content: " ";
  display: inline-block;
  width: 100%;
  height: 2px;
  background-color: #00bfff;
}

/*content-----------------------------------------------------*/
/*article-------------------------*/
.article_container{
  width: 840px;
}
.content_infinite_container{
  margin: 0 7% 0 0;
}
.figure:first-child{
  padding: 0;
}
.ant-list-item{
  border-bottom: #e8e8e8 solid 1px;
}
.figure{
  display: block;
  position: relative;
  width: 100%;
  padding: 20px 0;
  overflow: hidden;
}
.figure .article{
  float: left;
  margin-right: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
}
.figure .figcaption {
  font-size: 28px;
  padding: 0;
  font-weight: 600;
  margin: 0 0 2px;
  max-height: 3em;
  line-height: 1.5em;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
}
.figure a {
  text-decoration: none;
  color: inherit;
}
.figure .img{
  float: right;
  display:block;
  position: relative;
  width: 24%;
  height: 130px;
  border-radius: 10px;
  background-size: cover;
  background-position: center;
}
.figcaption a {
  text-decoration: none;
  color: inherit;
  background: 0 0;
 }
.subtitle{
  color: #b1b1b1;
  font-size: 14px;
}
.subtitle>a, .subtitle>span {
  display: inline-block;
  margin-right: 18px;
}
/*aside-----------------------------------------------------*/
.aside{
  width: 290px;
}
/*search---------------*/
.search_container{
  width: 100%;
  position: relative;
  margin: 0 0 13px;
  height: 42px;
  border-radius: 26px;
  border: #e8e8e8 solid 1px;
  border-right: none;
  background-color: #fff;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  -webkit-transition-delay: .1s;
  transition-delay: .1s;
}
.search_container:before {
  opacity: 0;
  float: left;
  position: absolute;
  height: 30px;
  width: 1px;
  left: -1px;
  top: 22px;
  content: "";
  background-color: #e8e8e8;
  -webkit-transition: opacity ease .5s;
  transition: opacity ease .5s;
}
.search_container:after {
  opacity: 0;
  float: right;
  position: absolute;
  height: 30px;
  width: 1px;
  right: -1px;
  top: 22px;
  content: "";
  background-color: #e8e8e8;
  -webkit-transition: opacity ease .5s;
  transition: opacity ease .5s;
}
.search{
  width: 145px;
  height: 40px;
  line-height: 40px;
  margin-left: 52px;
  border: none;
  outline: 0;
  font-size: 16px;
  color: #000;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  box-sizing: border-box;
}

.search_icon {
  position: absolute;
  z-index: 10;
  top: 0;
  left: 0;
  display: inline-block;
  width: 42px;
  height: 42px;
  overflow: hidden;
  -webkit-transition: width ease .5s;
  transition: width ease .5s;
}
.search_icon:before {
  position: absolute;
  top: 0;
  left: 0;
  content: " ";
  display: inline-block;
  width: 42px;
  height: 42px;
  background: url(data:image/png;base64,) no-repeat center;
  background-size: 15px 15px;
}
.search_btn {
  width: 74px;
  height: 42px;
  line-height: 42px;
  background-color: #363636;
  color: #fff;
  font-size: 14px;
  border-bottom-right-radius: 26px;
  border-top-right-radius: 26px;
  text-align: center;
  cursor: pointer;
  border: 1px solid #363636;
  box-sizing: border-box;
  float: right;
}

.search_assist_list {
  position: absolute;
  top: 50px;
  left: -1px;
  z-index: 10;
  width: 100%;
  height: 0;
  border-radius: 0 0 26px 26px;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
  border-color: transparent;
}
/*热门列表-----------------------------*/
.channel {
  margin-top: 30px;
  color: #3b3b3b;
}
.channel_title {
  height: 18px;
  line-height: 18px;
  font-size: 18px;
  font-weight: 600;
}
.channel_title:before {
  content: "";
  display: inline-block;
  float: left;
  width: 4px;
  height: 18px;
  margin-right: 10px;
  background-color: #FB4747;
}
.channel_list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
.channel_list ul.channel_list_show {
  display: block;
  color: #ababab;
}
.channel_list li {
  display: block;
  padding: 15px 0;
  line-height: 1.4em;
  max-width: 290px;
  word-wrap: break-word;
}
.channel_list li>a {
  color: #000;
  text-decoration: none;
  font-size: 18px;
  line-height: 1.4em;
  font-weight: 500;
}
.channel_list .channel_subtitle {
  color: #ababab;
  height: 25px;
  line-height: 25px;
}
.channel_subtitle>*, .channel_subtitle>a {
  margin-right: 10px;
  font-size: 14px;
  color: #ababab;
}
/*热搜词------------------------*/
.topSearch {
  padding: 0 0 20px;
  border-bottom: #e8e8e8 solid 1px;
}
.topSearch .channel_title {
  margin: 20px 0 32px;
}
.topSearch>span {
  display: block;
  margin: 0 14px 8px 0;
  height: 16px;
  line-height: 16px;
  font-size: 14px;
}
.topSearch>span a{
    color: #000;
}
.topSearch>span a:hover{
  color: #00bfff;
}

/*关于-----------------------*/
.aboutLn{
  padding: 0 0 20px;
  border-bottom: #e8e8e8 solid 1px;
}
.aboutLn .channel_title {
  margin: 20px 0 32px;
}
.about_wrap {
  width: inherit;
  display: block;
  height: auto;
  font-size: 0;
  margin: 0 auto;
  text-align: center;
}
.about_box {
  display: inline-block;
}
.about_box a{
  color: #000;
}

.about_bg {
  width: 50px;
  height: 50px;
  background-size: contain;
  background: url(data:image/png;base64,) center no-repeat;
  margin: 0 18px 10px;
}
/*固定工具---------------------*/
.fixed_tool.show {
  opacity: 1;
}

.fixed_tool {
  position: fixed;
  opacity: 0;
  bottom: 20px;
  right: 100px;
  -webkit-transition: all ease .5s;
  transition: all ease .5s;
}
.fixed_tool img {
  display: block;
  width: 50px;
  height: 50px;
  cursor: pointer;
}
.fixed_tool>* {
  display: block;
  margin-top: 20px;
}
/*注册页面*/
.register-root{
  position: relative;
  background: #e8e8e8;
  padding: 20px 0;
}
.register_brand {
  width: 50%;
  height: 850px;
  background-size: cover;
  background:url(/static/media/brand_bg.92d9e494.jpg) no-repeat 50%;
  color: #fff;
  padding: 160px 0;
  text-align: center;
}
.register_brand h2 {
  font-weight: 400;
  font-size: 24px;
  margin-bottom: 20px;
  color: inherit;
}
.register_brand p {
  font-size: 14px;
}
.register_brand .line {
  width: 36px;
  height: 2px;
  background: #fff;
  margin: 420px auto 25px;
}

.register-root .register_box{
  position: absolute;
  top: 160px;
  left: 50%;
  width: 480px;
  min-height: 530px;
  border-radius: 4px;
  background: #fff;
  padding: 50px 80px;
}

.form_title {
  font-weight: 400;
  font-size: 24px;
  color: #121212;
  text-align: center;
  margin-bottom: 40px;
}

